                  <div className="page1-title">
                    <span className="title" style={{ "--p": 6 }}>Web Scraping</span>
                    <img src="https://github.com/abhishek12234/Abhishek-portfolio/assets/117164257/2641a218-89a8-47cb-b348-b31801b83088" />
                  </div>

