import React from 'react'
import "./css/About.css"
import { useState, useEffect } from 'react'
import img_home from "./assets/my_home_img (1).png"
import Line from "./assets/Line.png"
import Line2 from "./assets/Line2.png"
import leetcode from "./assets/leetcode_icon.png"
import Hackerrank from "./assets/hackerrank_icon.png"
import indus from "./assets/indus.png"
import sage from "./assets/sage_logo.png"

export default function About(props) {
  const [about_popup, set_about_popup] = useState(false)


  const [scroll, set_scroll] = useState(false)
  useEffect(() => {
    if (props.activeSection !== "about") {
      set_about_popup(false)

    }
  })
  useEffect(() => {
    document.querySelector(".About-container").style.filter = about_popup ? "blur(15px)" : "";

  }, [about_popup]);


  return (
    <>
      <section id="about" className="about_section" style={{ "--height":about_popup?"175vh":"",background: props.switch_modes === 'dark' ? "" : "rgba(0, 0, 0, 0.05)" }}>
        <div className="about-outer-container">
          <div className={`About-container`} id="about-back">


            <img className={`about-image ${props.activeSection === "about" ? "active_about-image" : ""}`} src={img_home} />


            <div className="About-content">

              <h4 className={props.activeSection === "about" ? "active_h4" : ""} style={{ color: props.switch_modes === "dark" ? "" : "#484747" }}>About<span> Me</span></h4>
              <h3 className={props.activeSection === "about" ? "active_h3" : ""} style={{ color: props.switch_modes === "dark" ? "" : "#484747" }}>B.Tech. Student</h3>
              <div className='about-para'>
                <p className={`para ${props.activeSection === "about" ? "active_para" : ""}`} style={{ color: props.switch_modes === "dark" ? "" : "#484747" }}>As an expert in Python programming language with a focus on AI and machine learning, I have built several projects in these areas. Recently, I have also been exploring the world of web development, by learning JavaScript, CSS, HTML and React </p>
              </div>

              <button onClick={() => { set_about_popup(about_popup ? false : true) }} className={`btn ${props.activeSection === "about" ? "active_btn" : ""}`} style={{ display: "flex", "--shadow": props.switch_modes === "dark" ? "#0ef" : "#484747", backgroundColor: props.switch_modes === "dark" ? "" : "#484747", color: props.switch_modes === "dark" ? "" : "#0ef" }}><b> Read more</b></button>

            </div>

          
          </div>

          

          <div className={`about_popup_container ${about_popup ? "about_popup_container_active" : ""}`}>

            <div className={`popup_about ${about_popup ? "popup_about_active" : ""}`} style={{ backgroundColor: props.switch_modes === "dark" ? "" : "#484747" }}>
              <h3>Coding <span>Platforms</span></h3>
              
              <div className='popup_about_content1'>

                <div className="leetcode_container">
                  <img className="leetcode" src={leetcode} />
                  <div className="leetcode_content">
                    <h5>leetcode <span>Profile</span></h5>
                    <p>Continuously improving problem-solving skills Exploring diverse algorithms and data structures</p>
                    <p style={{color:"#04edfe",fontSize:"15px",marginTop:"-5px"}}><b>Questions Solved: </b><span style={{color:"white"}}>90+</span></p>
                    <a target="_blank" href="https://leetcode.com/abhishekindore786/" className={`btn`} style={{ display: "flex", "--shadow": props.switch_modes === "dark" ? "#0ef" : "#484747" }}><b>open</b></a>
                  </div>
                </div>

                <div className="leetcode_container">
                  <img className="leetcode" src={Hackerrank} />
                  <div className="leetcode_content">
                    <h5>HackerRank <span>Profile</span></h5>
                    <p>Passionate problem solve and learning database manipulation and management</p>
                    <p style={{color:"#04edfe",fontSize:"14px",marginTop:"-5px"}}><b>Problem Solving: </b><span style={{color:"white"}}>4 Star</span></p>
                    <p style={{color:"#04edfe",fontSize:"14px",marginTop:"-10px"}}><b>My SQL: </b><span style={{color:"white"}}>5 Star</span></p>
                    <a target="_blank" href="https://www.hackerrank.com/abhishekindore71" className={`btn`} style={{ display: "flex", "--shadow": props.switch_modes === "dark" ? "#0ef" : "#484747" }}><b>open</b></a>
                  </div>
                </div>

              </div>
              <img className="about_line" src={Line} />
              <img className="about_line1" src={Line2} />
              <img className="about_line2" src={Line} />
              <img className="about_line3" src={Line2} />
            </div>

            <div onClick={() => set_about_popup(about_popup ? false : true)} className={`assitant_button_container1 ${about_popup ? "assitant_button_container_active" : ""}`}>


              <a target="_blank" style={{ textDecoration: "None" }} className="assitant_button1">
                <h1>CLOSE</h1>
              </a>

            </div>

            <div className={`popup_about1 ${about_popup ? "popup_about_active" : ""}`} style={{ backgroundColor: props.switch_modes === "dark" ? "" : "#484747" }}>

              <h3>My <span>Education</span></h3>
              <div className='popup_about_content2'>

                <div className="education_container">
                  <img className="indus" src={indus} />
                  <div className="education_content">
                    <h4>Indus World <span>School</span></h4>
                    <h5>Senior Secondary, CBSE</h5>
                    <p><b>Subject:</b> <span>Physics, Chemistry, Maths</span></p>
                    <p><b>Score: </b><span>85.6%</span></p>
                  </div>
                </div>

                <div className="education_container">
                  <img className="sage" src={sage} />
                  <div className="education_content">
                    <h4>SAGE <span>University</span></h4>
                    <h5>B.Tech Computer Science</h5>
                    <p><b>Subject:</b> <span>Artifical Intelligence and Data Science</span></p>
                    <p><b>Score: </b><span>8.5 CGPA</span></p>
                  </div>
                </div>



              </div>
              <img className="about_line" src={Line} />
              <img className="about_line1" src={Line2} />
              <img className="about_line2" src={Line} />
              <img className="about_line3" src={Line2} />
            </div>
          </div>
        </div>
        
      </section>

    </>
  )
}
