import React, { useState, useEffect } from 'react';

import Homepage from './component/Homepage';
import Navebar from './component/Navebar';
import Skills from './component/Skill';
import Certificate from './component/Certificate';
import About from './component/About';
import Particle from './component/Particle';
import Contacts from './component/Contacts';
import Projects from './component/Projects';

function ScrollComponent({ active, children }) {
  return <div style={{ display: active ? 'block' : 'none' }}>{children}</div>;
}

function App() {
  const [switch_modes, set_modes] = useState('dark');
  const [curr_link, change_link] = useState('1');
  const [backgound_nav, set_backgound_nav] = useState('');

  const backgroundColor1 = ['2', '3', '4', '5', '6'].includes(curr_link)
    ? '#1f242f'
    : '#151820';
  

    useEffect(() => {
      // Scroll to top when the component mounts
      window.scrollTo(0, 0);
    }, []);


  
    // scroll handling part
    const sections = [
      { id: 'home', label: 'Section 1' },
      { id: 'about', label: 'Section 2' },
      { id: 'project', label: 'Section 1' },
      { id: 'skill', label: 'Section 2' },
      { id: 'contact', label: 'Section 2' },
      { id: 'certificate', label: 'Section 2' }
      
      // Add more section objects as needed
    ];
  
    const [activeSection, setActiveSection] = useState(sections[0].id);
  
    useEffect(() => {
      const handleScroll = () => {
        for (let i = sections.length - 1; i >= 0; i--) {
          const section = sections[i];
          const element = document.getElementById(section.id);
          const rect = element.getBoundingClientRect();
  
          if (rect.top <= window.innerHeight * 0.5 && rect.bottom >= window.innerHeight * 0.5) {
            setActiveSection(section.id);
            break;
          }
        }
      };
  
      window.addEventListener('scroll', handleScroll);
  
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, []);

    const handleSectionChange = (sectionId) => {
      setActiveSection(sectionId);
      const element = document.getElementById(sectionId);
      element.scrollIntoView({ behavior: 'smooth' });
    };
    // scrolling handling part
  return (
    <>
      <Particle switch_modes={switch_modes} set_modes={set_modes} style={{zIndex:-2}}/>
      <Navebar
        change_link={change_link}
        switch_modes={switch_modes}
        set_modes={set_modes}
        curr_link={curr_link}
        backgound_nav={backgound_nav}
        set_backgound_nav={set_backgound_nav}
        activeSection={activeSection}
        setActiveSection={setActiveSection}
        handleSectionChange={handleSectionChange}
      />
      <button className={`Scroll_to_top ${['about', 'contact', 'project',"skill","contact","certificate"].includes(activeSection)?"active_Scroll":""}`} onClick={()=>{window.scrollTo(0, 0);}} style={{ backgroundColor: switch_modes === "dark" ? "" : "#484747" }}><i class="fa-solid fa-arrow-up"/></button>
      <div className='Main_container' style={{ background: switch_modes === 'dark' ? '#151820' : "#fdfdfd" }}>

      
          <Homepage switch_modes={switch_modes} set_modes={set_modes} />
        

        
         
          <About switch_modes={switch_modes} set_modes={set_modes} activeSection={activeSection}/>
    
        
        <Projects activeSection={activeSection} switch_modes={switch_modes} set_modes={set_modes} handleSectionChange={handleSectionChange} />
        

 
        <Skills activeSection={activeSection}  switch_modes={switch_modes} set_modes={set_modes} />
        <Certificate activeSection={activeSection} switch_modes={switch_modes} set_modes={set_modes} />
        <Contacts activeSection={activeSection} switch_modes={switch_modes} set_modes={set_modes}/>
        

        </div>
        
        
       
       
        
        
      
    </>
  );
}

export default App;
