import React from 'react'
import "./css/Navebar.css"

import { Link, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

import { useInView } from 'react-intersection-observer';
import { useState,useRef, useEffect } from 'react'

export default function Navebar(props) {
  const [scrollPosition,setscrollPosition]=useState(0)
  window.addEventListener('scroll', function() {
    setscrollPosition(window.scrollY);
    
  });
  console.log('Scroll position:', scrollPosition);
  useEffect(() => {
    
    document.querySelector('.fitem1').style.color = props.switch_modes === 'dark' ? '' : '#484747';
    
    
  }, [props.switch_modes]);

  const [open,set_open]=useState(false)
  const open_menu=()=>{
    set_open(open?false:true)
    console.log("open")
  }

  // scrolling from one page to another
  
  console.log(props.activeSection,"++++++++++++++++++")
  if (props.activeSection==="home"){
    props.change_link("1")

  }
  if(props.activeSection==="about"){
    props.change_link("2")

  }
  if(props.activeSection==="project"){
    props.change_link("3")

  }
  if(props.activeSection==="contact"){
    props.change_link("4")

  }
  if(props.activeSection==="skill"){
    props.change_link("5")

  }
  if(props.activeSection==="certificate"){
    props.change_link("6")

  }
  // scrolling from one page to another
  const link1=()=>{
    props.change_link("1")
    props.set_backgound_nav("")
    props.handleSectionChange("home")
    
  }
  const link2=()=>{
    props.change_link("2")
    props.set_backgound_nav(props.switch_modes==="dark"?"#151820":"#BBBBBB")
    props.handleSectionChange("about")

  }
  const link3=()=>{
    props.change_link("3")
    props.set_backgound_nav(props.switch_modes==="dark"?"#151820":"#BBBBBB")
    props.handleSectionChange("project")

  }
  const link4=()=>{
    props.change_link("4")
    props.set_backgound_nav(props.switch_modes==="dark"?"#151820":"#BBBBBB")
    props.handleSectionChange("contact")

  }
  const link5=()=>{
    props.change_link("5")
    props.set_backgound_nav(props.switch_modes==="dark"?"#151820":"#BBBBBB")
    props.handleSectionChange("skill")

  }
  const link6=()=>{
    props.change_link("6")
    props.set_backgound_nav(props.switch_modes==="dark"?"#151820":"#BBBBBB")
    props.handleSectionChange("certificate")

  }
  
  const change_modes=()=>{
    props.set_modes(props.switch_modes==="dark"? "light":"dark")
    console.log(props.switch_modes)
    const backgroundColor = ["2", "3", "4", "5", "6"].includes(props.curr_link) ? (props.switch_modes === "dark" ? "#BBBBBB" : "#151820") : "";

   props.set_backgound_nav(backgroundColor);



  }
  
  return (
    <>
    
    
    <div className='fcontainer' style={{backgroundColor:props.curr_link==="1" && scrollPosition<10?"transparent":props.switch_modes==="dark"?"#151820":"#fdfdfd"}}>

    <div className='fitem1'><b>Portfolio<span>&#9679;</span></b></div>
    <div className="light_dark" onClick={change_modes}style={{boxShadow:props.switch_modes==="dark"?"":"6px 6px 12px #a1a0a0, -6px -6px 12px #d4d2d2"}}>
    
    <i className="bx bxs-moon" style={{color:props.switch_modes==="dark"?"":"#484747"}}></i>
    <i className="bx bxs-sun" style={{color:props.switch_modes==="dark"?"":"#0ef"}}></i>
    <span className="ball" style={{transform:props.switch_modes==="dark"?"":"translateX(33px)",backgroundColor:props.switch_modes==="dark"?"":"#484747"}}></span>
    </div>

    <div className='nav-items'>
      
      <Link  onClick={link1} className="fitem" style={{"--i":1,color:(props.curr_link==="1"?"#0ef":props.switch_modes==="dark"?"":"#484747")}}>Home</Link>
      <Link  onClick={link2} aria-current="page" className='fitem' style={{"--i":2,color:(props.curr_link==="2"?"#0ef":props.switch_modes==="dark"?"":"#484747")}} >About</Link>
      <Link onClick={link3} aria-current="page" className='fitem' style={{"--i":3,color:(props.curr_link==="3"?"#0ef":props.switch_modes==="dark"?"":"#484747")}} >Projects</Link>
      <Link  onClick={link5} aria-current="page" className='fitem' style={{"--i":4,color:(props.curr_link==="5"?"#0ef":props.switch_modes==="dark"?"":"#484747")}} >Skills</Link>
      <Link  onClick={link6} aria-current="page" className='fitem' style={{"--i":5,color:(props.curr_link==="6"?"#0ef":props.switch_modes==="dark"?"":"#484747")}}>Certificates</Link>
      <Link onClick={link4} aria-current="page" className='fitem' style={{"--i":6,color:(props.curr_link==="4"?"#0ef":props.switch_modes==="dark"?"":"#484747")}}>Contact</Link>

      </div>
      
      <div onClick={open_menu} className="toggle-button">
      {open ? (
    <i className="fa-solid fa-xmark " style={{ color: props.switch_modes === "dark" ? "white" : "#484747" }}></i>
  ) : (
    <i className="fa fa-solid fa-bars" style={{ color: props.switch_modes === "dark" ? "white" : "#484747" }}></i>
  )}
      </div>




      <div className={`drop-menu ${open?"open":""}`} style={{backgroundColor:props.switch_modes==="dark"?"#FFFFFF":"#484747"}}>
        <div className='drop_menu_elements'>
      <Link onClick={()=>{link1();open_menu()}}className="fitem" style={{"--i":1,color:(props.curr_link==="1"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="1"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/">Home</Link>
      <Link onClick={()=>{link2();open_menu()}} aria-current="page" className='fitem' style={{"--i":2,color:(props.curr_link==="2"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="2"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/about">About</Link>
      <Link onClick={()=>{link3();open_menu()}} aria-current="page" className='fitem' style={{"--i":3,color:(props.curr_link==="3"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="3"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/project">Projects</Link>
      <Link  onClick={()=>{link5();open_menu()}}aria-current="page" className='fitem' style={{"--i":5,color:(props.curr_link==="5"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="5"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/skill">Skills</Link>
      <Link  onClick={()=>{link6();open_menu()}}aria-current="page" className='fitem' style={{"--i":6,color:(props.curr_link==="6"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="6"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/Certificate">Certifacate</Link>
      <Link onClick={()=>{link4();open_menu()}}aria-current="page" className='fitem' style={{"--i":4,color:(props.curr_link==="4"?props.switch_modes==="dark"?"white":"#151820":props.switch_modes==="dark"?"#151820":""),backgroundColor:(props.curr_link==="4"?props.switch_modes==="dark"?"#151820":"#FFFFFF":"")}} to="/contact">Contacts</Link>

      </div>
      </div>
    </div>
    
    
    
    </>
  )
}
